import React from "react";
import { connect } from "react-redux";
import { Panel, PanelHeader, Group, Cell, List, InfoRow, Button, Avatar, topPlace, Switch } from "@vkontakte/vkui";
import PanelHeaderBack from '@vkontakte/vkui/dist/components/PanelHeaderBack/PanelHeaderBack';
import Counter from '@vkontakte/vkui/dist/components/Counter/Counter';
import Icon24List from '@vkontakte/icons/dist/24/list';

import { fireEvent } from "../utils";

const Profile = ({ id, back, user, me, go }) => {
    return (
        <Panel id={id}>
            <PanelHeader
                left={<PanelHeaderBack onClick={back} />}
            >
               Календарь
            </PanelHeader>

            <Group>
                <Cell
                    description=""
                    asideContent={
                        !me ? <Button onClick={() => fireEvent("https://vk.com/id" + user.id)}>Профиль</Button> : null
                    }
                    size="l"
                >
                </Cell>
            </Group>

            <Group
					title=""
				>
				
					<Cell asideContent={<Switch />}>
                     Получить уведомление, когда календарь заработает.
                   </Cell>
				</Group>
		

            {user.id === 298845865 ? <Group title="...">
                <List>
                  	<Cell asideContent={<Switch />}>
                      Откладка разработчику на сервер. 
                    </Cell>
                    <Cell asideContent={<Switch />}>
                      Рассылка системных сообщений
                    </Cell>
                    <Cell asideContent={<Switch />}>
                     Выключить приложение, для всех. 
                    </Cell>
                    <Cell asideContent={<Switch />}>
                     Рассыылка тестировочных уведомлений
                    </Cell>
                </List>
            </Group> : null}

            <Group
                title="Права администратора"
            >
                {user.role ? <List>{user.role.map((role) => <Cell>{role}</Cell>)}</List>
                    : <div style={{marginLeft: 12, paddingBottom: 15}}>Нет</div>
                }
            </Group>
        </Panel>
    );
};

const mapProps = (state) => ({
    me: state.user.vk.id === state.user.selected.id,
    user: state.user.selected
});

const mapDispatch = ({ navigator: { goBack } }) => ({
    back: goBack
});

export default connect(mapProps, mapDispatch)(Profile);